import React from 'react'
import { Link, NavLink } from 'react-router-dom'
import logo from '../../assets/images/logo-dark.webp'
export default function Navbar({Userdata ,logout}) {

    return <>

        <nav className="navbar navbar-expand-lg navbar-light nav-bg">
            <div className="container">
                <Link className="navbar-brand  font-lg fw-bolder" to={'/'}>
                    <img src={logo} alt="Noxe" />
                </Link>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="fa-solid fa-bars text-white fa-xl"></span>
                </button>
                <div className="collapse  pt-2 navbar-collapse" id="navbarSupportedContent">
                    {Userdata !== null ? <>
                       <ul className="navbar-nav me-auto mb-2 font-sm mb-lg-0">
                        <li className="nav-item">
                            <NavLink  className={({ isActive }) => (isActive ? 'active nav-link' : 'inactive nav-link')} to="/">Home</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className="nav-link" to="Movies">Movies</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className="nav-link" to="Tvshow">Tvshow</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className="nav-link" to="People">People</NavLink>
                        </li>
                    </ul>
                    </>:''}
                    <ul className="navbar-nav ms-auto mb-2 font-sm mb-lg-0">
                    {Userdata == null ? <>
                        <li className="nav-item">
                            <NavLink className="nav-link" to="Login">Login</NavLink>
                        </li>

                        <li className="nav-item">
                            <NavLink className="nav-link" to="Register">Register</NavLink>
                        </li>
                        </>:<>
                        <li className="nav-item">
                            <span onClick={logout} className="nav-link cursor-pointer">Logout</span>
                        </li>
                        </>}
                        </ul>
                </div>
            </div>
        </nav>

    </>
}
