import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Helmet } from 'react-helmet'
import { useParams } from 'react-router-dom'

export default function MoviesDetails() {
    const [Loading, setLoading] = useState(false)

    let { id, mediaType } = useParams()
    const [details, setdetails] = useState([])

    async function GetTrending(mediaType, id) {
        setLoading(true)
        let { data } = await axios.get(`https://api.themoviedb.org/3/${mediaType}/${id}?api_key=d733d821ff1062c1d16db10d624504c3&language=en-US`)
        console.log(data);
        setdetails(data);
        setLoading(false)
    }
    useEffect(() => {
        GetTrending(mediaType, id)
    }, [])
    return <>
     <Helmet>
      <title>MoviesDetails</title>
    </Helmet>
     {Loading ?
        <div className='loading d-flex align-items-center justify-content-center  position-fixed end-0  start-0 bottom-0 '>

            <div>
                <div className="lds-ring"><div></div><div></div><div></div><div></div></div>
            </div>      </div>
        : <>
            <div className='container'>
                <div className='row g-5 pt-4'>
                    <div className='col-lg-4 col-md-6' >
                        <div className='iamges' key={id}  >
                            {details.poster_path ? <img src={'https://image.tmdb.org/t/p/w500' + details.poster_path} className='w-100' alt="" /> : <img src={'https://image.tmdb.org/t/p/w500' + details.profile_path} className='w-100' alt="" />}
                        </div>
                    </div>


                    <div className='col-lg-7  col-md-6'>
                        <div className='title'>
                            <h3 className='text-main fw-bold'>{details.title} {details.name}</h3>
                            <p className='my-3 text-mutted'>{details.overview}{details.biography}</p>
                            <p className=' text-black font-lg h pt-1'>{ }</p>
                            {details.vote_average ? <h5 className='text-mutted'>vote average: {details.vote_average.toFixed(1)}</h5> : ''}
                            {details.vote_count ? <h5 className='text-mutted'>vote average: {details.vote_count}</h5> : ''}
                            {details.release_date ? <h5 className='text-mutted'>vote average: {details.release_date}</h5> : ''}
                            {details.place_of_birth ? <h5 className='text-mutted'>place_of_birth: {details.place_of_birth}</h5> : ''}
                            {details.birthday ? <h5 className='text-mutted'>birthday: {details.birthday}</h5> : ''}
                            {details.popularity ? <h5 className='text-mutted'>popularity: {details.popularity}</h5> : ''}
                        </div>

                    </div>
                </div>
            </div>
        </>
    }
    </>

}
