import logo from './logo.svg';
import './App.css';
import Login from './Components/Login/Login';
import Register from './Components/Register/Register';
import { RouterProvider, createBrowserRouter, createHashRouter } from 'react-router-dom';
import Layout from './Components/Layout/Layout';
import Movies from './Components/Movies/Movies';
import Tvshow from './Components/Tvshow/Tvshow';
import People from './Components/People/People';
import Forgetpassword from './Components/Forgetpassword/Forgetpassword';
import Home from './Components/Home/Home';
import { useState } from 'react';
import jwtDecode from 'jwt-decode';
import { Link, useNavigate } from 'react-router-dom'
import { useEffect } from 'react';
import ProdectRouter from './Components/ProdectRouter/ProdectRouter';
import HomeContextprovieder from './Context/Home';
import MoviesDetails from './Components/MoviesDetial/MoviesDetails';
import Search from './Components/Search/Search';
import ResetPassword from './Components/ResetPassword/ResetPassword';

function App() {
  useEffect(() => {
    if (localStorage.getItem('useToken') !== null) {
      SaveUserData()
    }
  }, [])
  const [Userdata, setUserdata] = useState(null)


  function SaveUserData() {
    let encodedToken = localStorage.getItem('useToken')
    let DecodeToken = jwtDecode(encodedToken);
    setUserdata(DecodeToken)
  }

  let routers = createHashRouter([
    {
      path: '', element: <Layout Userdata={Userdata} setUserdata={setUserdata} />, children: [
        { index: true, element: <ProdectRouter><Home /></ProdectRouter> },
        { path: 'Movies', element: <ProdectRouter><Movies /></ProdectRouter> },
        { path: 'Tvshow', element: <ProdectRouter><Tvshow /></ProdectRouter> },
        { path: 'Search', element: <ProdectRouter><Search /></ProdectRouter> },
        { path: 'MoviesDetails/:id/:mediaType', element: <ProdectRouter><MoviesDetails /></ProdectRouter> },
        { path: 'People', element: <ProdectRouter><People /></ProdectRouter> },
        { path: 'Login', element: <Login SaveUserData={SaveUserData} /> },
        { path: 'Register', element: <Register /> },
        { path: 'forgetpassword', element: <Forgetpassword /> },
        { path: 'resetPassword', element: <ResetPassword /> },

      ]
    }
  ])
  return <RouterProvider router={routers}></RouterProvider>

}

export default App;
