import React, { useState } from 'react'
import images from '../Home/home.module.css'
import { useFormik } from 'formik';
import axios from 'axios';
import { Link } from 'react-router-dom';
import MediaItem from '../MediaItem/MediaItem';

export default function Search() {
    const [Loading, setLoading] = useState(false)
    const [movies, setmovies] = useState([])

    let Formik = useFormik({
        initialValues: {
            search: ''

        },
        onSubmit: GetSearch,

    })
    async function GetSearch(med) {
        console.log(med);
        setLoading(true)
        let { data } = await axios.get(`https://api.themoviedb.org/3/search/movie?api_key=d733d821ff1062c1d16db10d624504c3&query=${med.search}`)
        console.log(data.results);
        setmovies(data.results);
        setLoading(false)

    }
    return <>
        <div className={images.header}>
            <div className={images.overlay}>
                <div className="d-flex align-items-center justify-content-center">
                    <div className='container'>
                        <h2 className='h3 text-white text-center'>Welcome.<p>Millions of movies, TV shows and people to discover. Explore now.
                        </p>
                        </h2>
                        <form id="inner_search_form" onSubmit={Formik.handleSubmit} className="position-relative w-100">
                            <input name="search" id='searchbar' value={Formik.values.search} onChange={Formik.handleChange} type="text" className={images.search + " form-control w-100 text-center rounded-5"} placeholder="Search for a movie , tv , person......" />
                            <input type="submit" className={images.btn + " btn rounded-5 position-absolute end-0 top-0"} value="Search" />
                        </form>
                    </div>
                </div>
            </div>
        </div>

        <div className='container'>
            <div className='row pt-5 '>
                {movies.map((items, index) => {
                    return <>   <div key={items.id} id={index} className='col-lg-2 col-md-4'>
                        <Link key={items.id} className='text-decoration-none text-white' to={`/MoviesDetails/${items.id}/movie`}>
                            <div className='position-relative'>
                                <div className=''>
                                    {items.poster_path ? <img src={'https://image.tmdb.org/t/p/w500' + items.poster_path} height={210} className='w-100' alt="" /> : <img src={'https://image.tmdb.org/t/p/w500' + items.profile_path} height={350} className='w-100' alt="" />}
                                </div>
                                <h3 className='h6 my-3'>{items.name} {items.title}</h3>
                                {items.vote_average ? <div className='vote top-0 end-0 position-absolute pt-1'>{items.vote_average.toFixed(1)}</div> : ''}
                            </div></Link>
                    </div>
                    </>

                })}
            </div>

        </div>

    </>
}
