import axios from "axios";
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { Helmet } from "react-helmet";

export default function Movies() {
  const [Loading, setLoading] = useState(false);
  const [Movie, setMovies] = useState([]);
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);

  async function GetTrending(p) {
    setLoading(true);
    let { data } = await axios.get(
      `https://api.themoviedb.org/3/discover/movie?api_key=d733d821ff1062c1d16db10d624504c3&language=en-US&sort_by=popularity.desc&include_adult=false&include_video=true&page=${p}`
    );

    setMovies(data.results);
    setPage(p);
    setTotalPages(data.total_pages);
    setLoading(false);
  }

  useEffect(() => {
    GetTrending(1);
  }, []);

  let nums = [...Array(10)]
    .map((_, i) => page - 5 + i)
    .filter((n) => n > 0 && n <= totalPages);

  return (
    <>
      <Helmet>
        <title>Movies</title>
      </Helmet>

      {Loading ? (
        <div className="loading d-flex align-items-center justify-content-center position-fixed end-0 start-0 bottom-0">
          <div className="lds-ring">
            <div></div>
            <div></div>
            <div></div>
            <div></div>
          </div>
        </div>
      ) : (
        <div className="container">
          <div className="row py-4 g-3">
            {Movie.map((m) => (
              <div key={m.id} className="col-lg-2 col-md-4">
                <Link
                  className="text-white text-decoration-none"
                  to={`/MoviesDetails/${m.id}/movie`}
                >
                  <div className="cared">
                    <div className="position-relative">
                      <img
                        src={"https://image.tmdb.org/t/p/w500" + m.poster_path}
                        height={210}
                        className="w-100"
                        alt=""
                      />
                      {m.vote_average ? (
                        <div className="vote top-0 end-0 position-absolute pt-1">
                          {m.vote_average.toFixed(1)}
                        </div>
                      ) : (
                        ""
                      )}
                    </div>
                  </div>
                  <h3 className="h6 my-3">{m.title}</h3>
                </Link>
              </div>
            ))}
          </div>

          {/* Pagination */}
          <nav aria-label="Page navigation">
            <ul className="pagination justify-content-center">
              {/* Previous */}
              <li className={`page-item ${page === 1 ? "disabled" : ""}`}>
                <button
                  className="page-link"
                  onClick={() => GetTrending(page - 1)}
                >
                  Previous
                </button>
              </li>

              {/* Dynamic numbers */}
              {nums.map((n) => (
                <li
                  key={n}
                  className={`page-item ${n === page ? "active" : ""}`}
                >
                  <button className="page-link" onClick={() => GetTrending(n)}>
                    {n}
                  </button>
                </li>
              ))}

              {/* Next */}
              <li
                className={`page-item ${page === totalPages ? "disabled" : ""}`}
              >
                <button
                  className="page-link"
                  onClick={() => GetTrending(page + 1)}
                >
                  Next
                </button>
              </li>
            </ul>
          </nav>
        </div>
      )}
    </>
  );
}
