import axios from "axios";
import React, { useEffect, useState } from "react";
import { Helmet } from "react-helmet";
import { Link } from "react-router-dom";

export default function People() {
  const [Loading, setLoading] = useState(false);
  const [People, setPeople] = useState([]);
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);

  async function GetTrending(p) {
    setLoading(true);

    let { data } = await axios.get(
      `https://api.themoviedb.org/3/person/popular?api_key=d733d821ff1062c1d16db10d624504c3&language=en-US&page=${p}`
    );

    setPeople(data.results);
    setPage(p);
    setTotalPages(data.total_pages);
    setLoading(false);
  }

  useEffect(() => {
    GetTrending(1);
  }, []);

  let nums = [...Array(10)]
    .map((_, i) => page - 5 + i)
    .filter((n) => n > 0 && n <= totalPages);

  return (
    <>
      <Helmet>
        <title>People</title>
      </Helmet>

      {Loading ? (
        <div className="loading d-flex align-items-center justify-content-center position-fixed end-0 start-0 bottom-0">
          <div className="lds-ring">
            <div></div>
            <div></div>
            <div></div>
            <div></div>
          </div>
        </div>
      ) : (
        <div className="container">
          <div className="row py-4 g-3">
            {People.map((p) => (
              <div key={p.id} className="col-lg-2 col-md-4">
                <Link
                  className="text-decoration-none text-white"
                  to={`/MoviesDetails/${p.id}/person`}
                >
                  <div className="card nav-bg border-0 cared">
                    <img
                      src={"https://image.tmdb.org/t/p/w500" + p.profile_path}
                      height={300}
                      className="card-img-top"
                      alt=""
                    />
                    <div className="card-body">
                      <p className="card-text">{p.name}</p>
                    </div>
                  </div>
                </Link>
              </div>
            ))}
          </div>

          {/* Pagination */}
          <nav aria-label="Page navigation example">
            <ul className="pagination justify-content-center">
              {/* Previous */}
              <li className={`page-item ${page === 1 ? "disabled" : ""}`}>
                <button
                  className="page-link"
                  onClick={() => GetTrending(page - 1)}
                >
                  Previous
                </button>
              </li>

              {/* Dynamic page numbers */}
              {nums.map((n) => (
                <li
                  key={n}
                  className={`page-item ${n === page ? "active" : ""}`}
                >
                  <button className="page-link" onClick={() => GetTrending(n)}>
                    {n}
                  </button>
                </li>
              ))}

              {/* Next */}
              <li
                className={`page-item ${page === totalPages ? "disabled" : ""}`}
              >
                <button
                  className="page-link"
                  onClick={() => GetTrending(page + 1)}
                >
                  Next
                </button>
              </li>
            </ul>
          </nav>
        </div>
      )}
    </>
  );
}
