import React from 'react'
import { Link } from 'react-router-dom'
import images from '../Home/home.module.css'

export default function MediaItem({ items }) {
  return <>
    <div key={items.id} className='col-lg-2 col-md-4'>
      <Link className='text-decoration-none text-white' to={`/MoviesDetails/${items.id}/${items.media_type}`}>
        <div className='cared w-75'>
          <div className='position-relative'>
            {items.poster_path ? <img src={'https://image.tmdb.org/t/p/w500' + items.poster_path} height={300} className='imagesmovies' alt="" /> : <img src={'https://image.tmdb.org/t/p/w500' + items.profile_path} height={350} className='imagesmovies' alt="" />}
            {items.vote_average ? <div className='vote top-0 end-0 position-absolute pt-1'>{items.vote_average.toFixed(1)}</div> : ''}

          </div>
          <h3 className='h6 my-3'>{items.name} {items.title}</h3>
        </div></Link>
    </div>


  </>
}
