import axios from "axios";
import { useFormik } from "formik";
import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import * as Yup from "yup";
import { Helmet } from "react-helmet";
export default function ResetPassword() {
  let BaseUrl = `https://ecommerce.routemisr.com`;

  let navigate = useNavigate();
  const [MessErrors, setMessErrors] = useState();
  const [BtnLoading, setBtnLoading] = useState(false);
  let validate = Yup.object({
    email: Yup.string()
      .required("email is required")
      .email("Invalid email address"),
    newPassword: Yup.string()
      .required("password is required")
      .matches(
        /^[A-Z][a-z0-9]{5,10}$/,
        "password must start with uppercase ...."
      ),
  });

  let formik = useFormik({
    initialValues: {
      email: "",
      newPassword: "",
    },
    validationSchema: validate,
    onSubmit: async (values) => {
      setBtnLoading(true);
      let { data } = await axios
        .put(`${BaseUrl}/api/v1/auth/resetPassword`, values)
        .catch((errors) => {
          setMessErrors(errors.response.data.message);
          setBtnLoading(false);
        });
      console.log(data);
      if (data.token) {
        setBtnLoading(false);
        navigate("/login");
      }
    },
  });

  return (
    <>
      <Helmet>
        <title>ResetPassword</title>
      </Helmet>
      <div className="container main">
        <div className="row bg-white p-4">
          <div className="col-md-5">
            <div className="input-box">
              <header className="h2">reset Password</header>
              {MessErrors ? (
                <div className="alert alert-danger">{MessErrors}</div>
              ) : (
                ""
              )}
              <form onSubmit={formik.handleSubmit}>
                <div className="input-field">
                  <input
                    type="email"
                    className="input"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    value={formik.values.email}
                    name="email"
                    id="email"
                    required
                  />
                  <label htmlFor="email">Email</label>
                  {formik.errors.email ? (
                    <div className="alert alert-danger mt-2">
                      {formik.errors.email}
                    </div>
                  ) : (
                    ""
                  )}
                </div>
                <div className="input-field">
                  <input
                    type="Password"
                    className="input"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    value={formik.values.newPassword}
                    name="newPassword"
                    id="newPassword"
                    required
                  />
                  <label htmlFor="newPassword">newPassword</label>
                  {formik.errors.newPassword ? (
                    <div className="alert alert-danger mt-2">
                      {formik.errors.newPassword}
                    </div>
                  ) : (
                    ""
                  )}
                </div>
                <div className="input-field">
                  {BtnLoading ? (
                    <button
                      type="button"
                      className="submit main-color  text-white"
                    >
                      <i className="fas fa-spinner fa-spin"></i>
                    </button>
                  ) : (
                    <button
                      disabled={!(formik.isValid && formik.dirty)}
                      type="submit"
                      className="submit mt-1"
                    >
                      Send Email
                    </button>
                  )}
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
